           <div>{{script?dayjs(script.date).format('YYYY-MM-DD HH:mm:ss'):'~'}}</div>
         </div>
       </div>
     </div>

     <div class="holders_transfers">
       <div class="holders_transfers_head">
         <a-radio-group v-model:value="radioValue" size="large" @change="onChange">
