           <a-radio-button value="transfers">Transfers</a-radio-button>
         </a-radio-group>
       </div>
       <a-table style="width: 1078px; overflow-x: auto" v-show="radioValue === 'holders'" :columns="holdersColumns" :data-source="holdersData">
         <template #bodyCell="{ column, text }">
