<script>
import { useRouter, useRoute, onBeforeRouteLeave } from "vue-router";
import {defineComponent, reactive, toRefs,onMounted} from "vue";
import {getHolders,getTrasfers,getToken,stringToHex} from '@/utils/api'
import {  notification } from 'ant-design-vue';
import { useStore } from 'vuex';

export default defineComponent({
  name: "InfoView",

  setup() {
    const Router = useRouter();
    const route = useRoute();
    const store = useStore();
    let dayjs = require('dayjs')
    const Data = reactive({
      radioValue: 'holders',
      script: null,
      page:1,
      loading:false,
      holdersColumns: [
        {title: 'Rank', dataIndex: 'rank', key: 'rank', width: 119,},
        {title: 'Address', dataIndex: 'address', key: 'address', width: 369, },
        {title: 'Percentage', dataIndex: 'percentage', key: 'percentage', width: 369, },
        {title: 'Value', dataIndex: 'value', key: 'value', width: 219, },
      ],
      holdersData: [],
      transfersColumns: [
        {title: 'id', dataIndex: 'id', key: 'id', width: 180,},
        {title: 'op', dataIndex: 'op', key: 'op', width: 100, },
        {title: 'From', dataIndex: 'from', key: 'from', width: 190, },
        {title: 'To', dataIndex: 'to', key: 'to', width: 190, },
        {title: 'amt', dataIndex: 'amt', key: 'amt', width: 120, },
        {title: 'Date Time', dataIndex: 'date', key: 'date', width: 280, align: 'right'},
      ],
      transfersData: [],
    });
    onMounted(() => {
      getToken(route.query.tick).then((response) => {
         if(response.code==0){
            Data.script = response.data
          }else{
            notification["error"]({message:"Error",description:response.msg});
          }
      })
      Methods.loadList()
    })
    const Methods = {
      omit(val) {
        let str = '';
        if(val.length > 15) {
          str = val.substr(0, 8) + "..." + val.substr(-8);
        }else {
          str = val
        }
        return str
      },
      loadList() {
        Data.page=1
        Data.tableLoading=true
        if(Data.radioValue=="holders"){
          getHolders(route.query.tick.trim(),Data.page).then((response) => {
            Data.tableLoading=false
            if(response.code==0){
              Data.holdersData = response.data.list
            }else{
              Data.tableLoading=false
              notification["error"]({message:"Error",description:response.msg});
            }
          })
        }else{
          getTrasfers(route.query.tick.trim(),Data.page).then((response) => {
            Data.tableLoading=false
            if(response.code==0){
              Data.transfersData = response.data.list
            }else{
              Data.tableLoading=false
              notification["error"]({message:"Error",description:response.msg});
            }
          })
        }
      },
      goback() {
        Router.go(-1)
      },
      onEthscribe() {
        if(true){
          window.notification["warning"]({message:"",description:"Coming Soon"});
          return;
        }
        if(!store.state.web3){
          store.dispatch("connectWallet")
          window.notification["error"]({message:"Error",description:"Connect your wallet"});
          return;
        }
        Data.loading = true;
        let json = stringToHex('{"p":"script","op":"mint","tick":"'+Data.script.tick.trim()+'","amt":"'+Data.script.limit+'"}');
        window.ethereum.request({
          method: 'eth_sendTransaction',
          params: [{from:store.state.wallet_address,to:store.state.toAddress,data:json}],
        }).then((txHash) => {
          window.notification["success"]({message:"",description:"Mint success"});
          Data.loading = false
        }).catch((error) =>  {
          Data.loading = false
        });
      },
      onChange() {
        Methods.loadList()
      }
    };
    return {
      ...toRefs(Data),
      ...Methods,
      dayjs
    }
  },
});

</script>

